<template>
  <movable-area :animation="false" :style="'height:' + height + 'upx'" class="movableArea">
    <view :style="'height: 100%;background: #5FC347;width:' + (oldx + 50) + 'px;'"></view>
    <movable-view class="movableView" :style="'height:' + (height - 2) + 'upx'" :class="isOk ? 'active' : ''" :disabled="isOk ? true : false" @touchend="onTouchend" :x="x" :y="y" direction="horizontal" @change="onChange"></movable-view>
  </movable-area>
</template>

<script>
export default {
  name: 'moveVerify',
  data() {
    return {
      x: 0,
      y: 0,
      oldx: 0,
      isOk: false,
      isChange: false, //change事件是否触发
      movableArea: 0, // 滑动区域的宽度
      movableView: 0 //滑块的宽度
    }
  },
  mounted() {
    var that = this
    uni
      .createSelectorQuery()
      .in(this)
      .select('.movableArea')
      .boundingClientRect(function (data) {
        that.movableArea = data.width
      })
      .exec()
    uni
      .createSelectorQuery()
      .in(this)
      .select('.movableView')
      .boundingClientRect(function (data) {
        that.movableView = data.width
      })
      .exec()
  },
  props: {
    height: {
      type: Number,
      default: () => 100
    }
  },
  methods: {
    onChange(e) {
      var { x } = e.detail
      this.oldx = x
    },
    // 验证成功
    onSuccess() {
      this.isOk = true
      this.$emit('onSuccess')
    },
    // 验证失败
    onFail() {
      this.$emit('onFail')
    },
    // 重置
    onReset() {
      this.isOk = false
      this.x = 0
      this.oldX = 0
    },
    onTouchend(e) {
      // 防止验证成功后还是触发
      if (this.isOk || this.oldx < 1) {
        return
      }
      this.x = this.oldx
      if (this.oldx + 2 > this.movableArea - this.movableView) {
        this.onSuccess()
      } else {
        this.$nextTick(() => {
          setTimeout(() => {
            this.x = 0
            this.oldx = 0
          }, 200)
          this.onFail()
        })
      }
    }
  }
}
</script>

<style lang="scss" scoped>
.movableArea {
  width: 100%;
  background: #eee;
  .movableView {
    height: 98upx;
    width: 120upx;
    border: 1upx #f0f0f0 solid;
    background: #fff no-repeat
      url('data:image/png;base64,')
      center;
  }
  .active {
    background-image: url('data:image/png;base64,');
  }
}
</style>
